import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.portfolio.pages.HomePage() }

    }
    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.bannerStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.GradientTagLineStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.buttonStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.ContactStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.formStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.footer)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.newsLetterBox)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.newEmailBox)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.NavBarStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.NavBarButtonStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.NavBarLinkStyle)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.P)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.projectCardImgBox)
        ctx.theme.registerComponentStyle(org.example.portfolio.styles.projectCardTextOverlay)
        ctx.stylesheet.registerKeyframes(org.example.portfolio.styles.zoomIn)
        ctx.stylesheet.registerKeyframes(org.example.portfolio.styles.upDownAnim)
        org.example.portfolio.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Portfolio Kotlin")
        ) { org.example.portfolio.MyApp {
                com.varabyte.kobweb.silk.defer.renderWithDeferred { router.renderActivePage() }
            }
        }
    }
}
